@-webkit-keyframes slidein {
  from {
    margin-left: 0%;
    width: 10%;
    height: 100%;
    
  }

  to {
    margin-left: 0%;
    width: 50%;
    height: 100%;
    
  }

  
}
@keyframes slidein {
  from {
    margin-left: 0%;
    width: 10%;
    height: 100%;
    
  }

  to {
    margin-left: 0%;
    width: 50%;
    height: 100%;
    
  }

  
}
@-webkit-keyframes slidein2 {
  from {
    margin-right: 0%;
    width: 0%;
  }

  to {
    margin-right: 0%;
    width: 40%;
  }

  
}
@keyframes slidein2 {
  from {
    margin-right: 0%;
    width: 0%;
  }

  to {
    margin-right: 0%;
    width: 40%;
  }

  
}



@-webkit-keyframes slidein3 {
